import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { notice } from "../../reducers/notificationReducer";
import { editoiGalleriaa,deleteGallerias } from "../../reducers/galleriaReducer";

function SingleGallery(){
    const id = useParams().id
    const galleria = useSelector(state => state.galleria)
    const target = galleria.find(galleria => galleria.id === id)
    const user = useSelector(state => state.currentUser)
    const dispatch = useDispatch()

    const [text,setText] = useState("")
    const [img,setImg] = useState("")
    const [loader,setLoader] = useState(false)


    const handleDelete = async () => {
        // const targetDel = data.find((n) => n.id === id)
         if (window.confirm(`haluatko poistaa kuvan?`)) {
           dispatch(deleteGallerias(id))
           dispatch(notice("poistit kuvan",3))
         }
       }
       

    function handleEdit(){

        if (window.confirm("haluatko varmasti muokata?")){
        const eventObj = {
            text: text,
            img: img
        }
        dispatch(editoiGalleriaa(eventObj,id))
        dispatch(notice("päivitit galleriaa",5))

      }
    }
    if(target && !loader){
        setImg(target.img)
        setText(target.text)
        setLoader(true)
    }
if(user && loader){
    return(
        <div className="etusivu">
        <h3 style={{color:"whitesmoke"}}>kuva linkki:</h3><input className="css-input" type="text" value={text} onChange={(e)=>setText(e.target.value)}></input>
        <h3 style={{color:"whitesmoke"}}>teksti kenttä:</h3><input className="css-input" type="text" value={img} onChange={(e)=>setImg(e.target.value)}></input>
        <button className="btn2" onClick={()=>handleEdit()}>muokkaa</button>
        <button className="btn2" type="button" onClick={() => handleDelete()}> poista</button>
        <img style={{width:"80vw",height:"80vh"}} src={target.img}></img>
        </div>
    )
}
else{
    return(<>
    <h2>error</h2>
    </>)
}
}
export default SingleGallery